import { Helmet } from "react-helmet";
import Header from "../ComponentChilds/Header";
import HeroSection from "../ComponentChilds/homepage/HeroSection";
import { lazy, Suspense } from "react";

const CategorySection = lazy(() =>
  import("../ComponentChilds/homepage/Category")
);
const ProductList = lazy(() =>
  import("../ComponentChilds/homepage/products/ProductList")
);
const BlogList = lazy(() => import("../ComponentChilds/homepage/Blogs"));
const AlmaxsportAbout = lazy(() =>
  import("../ComponentChilds/homepage/AllmaxsportAbout")
);
const Footer = lazy(() => import("../ComponentChilds/Footer"));

export default function HomePage({ basketItemCount, onSetBasketItemCount }) {
  return (
    <>
      <Helmet>
        <title>  |     </title>
        <meta
          name="google-site-verification"
          content="vLoybWMp_F1rG0-JbE-CRNs7KT1b-QccIuJr91CdzhA"
        />
        <meta
          name="description"
          content="                  "
        />
        <meta
          name="keywords"
          content=" ,  ,  ,   ,  ,   "
        />
        <meta name="robots" content="index, follow" />
        <link rel="canonical" href="https://allmaxsport.com/" />

        {/* Open Graph for Facebook */}
        <meta property="og:title" content="  |  " />
        <meta
          property="og:description"
          content="         "
        />
        <meta
          property="og:image"
          content="https://allmaxsport.com/og-image.jpg"
        />
        <meta property="og:url" content="https://allmaxsport.com/" />
        <meta property="og:type" content="website" />

        {/* Twitter Meta Tags */}
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content=" " />
        <meta
          name="twitter:description"
          content="          "
        />
        <meta
          name="twitter:image"
          content="https://allmaxsport.com/og-image.jpg"
        />

        {/* Structured Data: Organization + Navigation (encourages sitelinks) */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "Organization",
            name: " ",
            url: "https://allmaxsport.com/",
            logo: "https://allmaxsport.com/logo.jpg",
            contactPoint: {
              "@type": "ContactPoint",
              telephone: "+98-939-256-6494",
              contactType: "Customer Support",
              areaServed: "IR",
              availableLanguage: ["fa", "en"],
            },
            sameAs: [
              "https://instagram.com/allmaxsport",
              "https://t.me/yourpage",
              "https://wa.me/989392566494",
            ],
          })}
        </script>

        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "SiteNavigationElement",
            name: ["", "", "", " ", "  "],
            url: [
              "https://allmaxsport.com/",
              "https://allmaxsport.com/products",
              "https://allmaxsport.com/blogs",
              "https://allmaxsport.com/about",
              "https://allmaxsport.com/contact",
            ],
          })}
        </script>
      </Helmet>

      <Header basketItemCount={basketItemCount} />
      <HeroSection />
      <CategorySection />

      <Suspense
        fallback={
          <div className="text-center mt-10">  ...</div>
        }
      >
        <ProductList onAddToCart={onSetBasketItemCount} />
      </Suspense>

      <Suspense
        fallback={
          <div className="text-center mt-10">  ...</div>
        }
      >
        <BlogList />
      </Suspense>

      <Suspense
        fallback={
          <div className="text-center mt-10">   ...</div>
        }
      >
        <AlmaxsportAbout />
      </Suspense>

      <Suspense
        fallback={
          <div className="text-center mt-10">  ...</div>
        }
      >
        <Footer />
      </Suspense>
    </>
  );
}
